@font-face {
  font-family: "Open Sans";
  font-weight: 700;
  font-style: normal;
  font-display: swap;
  src: local("Open Sans"), url("../fonts/open_sans-bold.woff2") format("woff2"), url("../fonts/open_sans-bold.woff") format("woff"); }

@font-face {
  font-family: "Open Sans";
  font-weight: 600;
  font-style: normal;
  font-display: swap;
  src: local("Open Sans"), url("../fonts/open_sans-semibold.woff2") format("woff2"), url("../fonts/open_sans-semibold.woff") format("woff"); }

@font-face {
  font-family: "Open Sans";
  font-weight: 400;
  font-style: normal;
  font-display: swap;
  src: local("Open Sans"), url("../fonts/open_sans-regular.woff2") format("woff2"), url("../fonts/open_sans-regular.woff") format("woff"); }

@font-face {
  font-family: "Open Sans";
  font-weight: 300;
  font-style: normal;
  font-display: swap;
  src: local("Open Sans"), url("../fonts/open_sans-light.woff2") format("woff2"), url("../fonts/open_sans-light.woff") format("woff"); }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
          clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

*,
*::before,
*::after {
  -webkit-box-sizing: border-box;
          box-sizing: border-box; }

a {
  text-decoration: none; }

img {
  max-width: 100%;
  height: auto; }

.page {
  display: grid;
  grid-template-rows: -webkit-min-content 1fr -webkit-min-content;
  grid-template-rows: min-content 1fr min-content;
  min-height: 100%;
  margin: 0;
  font-style: normal;
  font-size: 18px;
  line-height: 34px;
  font-family: "Open Sans", "Arial", sans-serif;
  color: #000000; }
